import React from 'react'
import Backdrop from '../../assets/E3-bg.jpg'
import A3SFeatures from '../../components/A3SFeatures/A3SFeatures'
import A3SFAQ from '../../components/A3SFAQ/A3SFAQ'

export default function A3S() {
  return (
    <div className='bg-black'>
      <section className="  text-gray-400 body-font h-6/12 max-w-full overflowx-hidden">
        <div className='bg-yellow-300'>
          <img className='object-cover w-full h-96' src={Backdrop} alt='backdrop' />
        </div>

        <div className='bg-white absolute top-12 h-72 xl:top-40 xl:left-40  xl:w-5/12 text-black p-10 '>
          <h2 className='m-1 font-bold text-3xl l:text-5xl'>Enigma A3S</h2>
          <p className='m-1 font-medium text-xl l:text-4xl  pb-5 md:pb-10'>License best-in-class quantitative alphas across different trading styles and asset classes</p>
          <button class="flex-shrink-0 text-white l-5 bg-gray-800 border-0 py-2 px-8 focus:outline-none hover:bg-black rounded text-lg  sm:mt-0">Button</button>
        </div>
      </section>
      <section className="text-gray-300 body-font pb-10 pt-10 ">
        <div className='mx-10 lg:mx-40 lg:mt-20 text-lg '>
          <p>ENIGMA Alpha-as-a-Service (ENIGMA A3S) is web service where users can license quantitative trading and investment strategies to capitalize on the trades they generate.</p>
          <p>Each strategy or "alpha" is the result of rigorous data-driven research and testing, and the premise of each alpha and its historical performance is made available to help users make an informed decision.</p>
        </div>
        <div className='00 mx-10 mt-12 lg:mx-40 lg:flex lg:flex-row lg:flex-wrap lg:justify-around'>
          <p className=" mb-5 lg:w-6/12 lg:m-0 lg:p-5 text-xl text-center ">Nearly <strong>40</strong> alphas to choose from for every kind of investor</p>
          <p className=" mb-5 lg:w-6/12 lg:m-0 lg:p-5  text-xl text-center"><stong>Asset classes:</stong> Equity Cash, Equity Futures, Index Options</p>
          <p className=" mb-5 lg:w-6/12 lg:m-0 lg:p-5 text-xl text-center">Both <strong>Intraday and Positional </strong> alphas</p>
          <p className=" mb-5 lg:w-6/12 lg:m-0 lg:p-5 text-xl text-center"><strong>Exchanges:</strong> NSE, MCX</p>
        </div>
      </section>
      <A3SFeatures />
      <section class="text-gray-400  body-font">
        <div class="container mx-auto flex px-5 py-24 items-center justify-center flex-col">
          <div class="text-center lg:w-2/3 w-full">
            <h1 class="title-font sm:text-4xl text-3xl mb-4 font-bold text-white">Pricing</h1>
            <h2 class="title-font sm:text-lg text-base mb-4 font-medium text-white">A3S alpha licenses are billed on a per-trigger-day-per-unit basis. Trigger day is a day when the alpha triggers a trade. Unit is liquidity measure.</h2>
            <p class="leading-relaxed mb-8">Consider an intraday alpha DUMMY-ALPHA priced at 5 TNG that trades RELIANCE CASH on high momentum. Let's say you license 3 units of this alpha — you will only be billed when it triggers a trade. So let's say on Monday RELIANCE was sideways and no trade triggered — then no billing for you! Then on Tuesday it was showing extremely strong momentum — trade is triggered and you are charged 15 TNG (5 TNG/unit x 3 units). Now irrespective of how many trades it triggers on that day — 1, 5, 10 — you are charged only once for the trigger day (note: execution via E3 billed separately, E3 billing not included). Now after Tuesday, you don't like it and unlicense it, then you aren't billed anymore! So you only pay for what you use!!!</p>
          </div>
        </div>
      </section>
      <A3SFAQ />
    </div>
  )
}
