import React, { useState } from 'react'
import ToggleButton from './ToggleButton/ToggleButton'
import './MobileNavbar.css'
import Logo from '../../assets/Enigma-logo.png'
import { Link } from 'react-router-dom'

const MobileNavbar = () => {
  const [drawerOpen, setDrawerOpen] = useState(false)
  const toggleClickHandler = () => {
    setDrawerOpen(!drawerOpen)
  }
  return (
    <>
      {!drawerOpen && (
        <div className='container text-gray-400 mx-auto  p-5 fixed bg-black flex-col md:flex-row items-center border-b-2 z-10 border-gray-300 max-w-full body-font'>
          <ToggleButton drawerToggler={toggleClickHandler} />
        </div>
      )}
      {drawerOpen && (
        <div className='container text-gray-400 mx-auto max-h-full p-5 fixed bg-black slide-in-top flex-col md:flex-row items-center border-b-2 z-10 border-gray-300 max-w-full body-font'>
          <ToggleButton drawerToggler={toggleClickHandler} />
          <div className='h-screen w-12/12 flex flex-col  items-center justify-center pb-32 pt-10' >
            <div className=' flex flex-col items-center ' >
              <Link className='my-5' to='/' onClick={toggleClickHandler}>
                <div>
                  <img className='w-20' src={Logo} />
                </div>
              </Link>

              <Link className='my-5' to='/a3s' onClick={toggleClickHandler}>
                <a class="hover:text-white  min-w-4/12 text-2xl ">A3S</a>
              </Link>
              <Link className='my-5' to='/e3' onClick={toggleClickHandler}>
                <a class="hover:text-white  min-w-4/12 text-2xl ">E3</a>
              </Link>
              <Link className='my-5' to='/turing' onClick={toggleClickHandler}>
                <a class="hover:text-white  min-w-4/12 text-2xl ">Turing</a>
              </Link>
            </div>
            <button class="inline-flex items-center min-w-min bg-gray-800 text-2xl border-0 py-1 px-3 focus:outline-none hover:bg-black hover:text-white rounded  mt-4 md:mt-0">Sign Up
          </button>
          </div>
        </div>
      )}

    </>

  )
}

export default MobileNavbar
