import React from 'react'
import Faq from 'react-faq-component'

export default function E3FAQ() {
  const data = {
    title: 'FAQs',
    rows: [
      {
        title: "What are Alpha Units?",
        content: 'The maximum amount of capital that an alpha can accommodate is limited, post which its performance will begin to degrade. This point is called the maximum liquidity of that alpha. This is a limited resource and pricing in terms of alpha units permits us to regulate this liquidity usage! :D'
      },
      {
        title: "Why do prices of alphas change?",
        content: "Based on supply-demand, slippages etc. the marketplace algorithm automatically adjusts the per unit pricing of alphas on a monthly basis. Users are informed of such changes to any licensed alphas when this happens and they have 7 days to revise their alpha allocations before the new pricing is implemented."
      },
      {
        title: 'Do I pay ever day?',
        content: 'No, only on triggered days'
      },
      {
        title: 'Why is there a max?',
        content: 'Liquidity limited'
      },
      {
        title: 'Can I list my own alphas?',
        content: 'Not rn. Maybe later'
      },
      {
        title: 'How to trade these alphas?',
        content: 'via E3. other options coming soon'
      },
      {
        title: 'Can I paper trade?',
        content: 'Not yet...'
      },
    ]
  }
  const styles = {
    bgColor: '#000',
    titleTextColor: 'white',
    rowTitleColor: 'white',
    rowContentColor: '#9CA3AB',
    arrowColor: 'white',
    rowContentPaddingTop: '7px',
    rowContentPaddingBottom: '7px',
    rowContentPaddingLeft: '30px',
    rowContentPaddingRight: '30px',
  }
  return (
    <section className='bg-black flex justify-center pt-5 pb-20'>
      <div className='w-11/12 lg:w-8/12 '>
        <Faq data={data} styles={styles} />
      </div>

    </section>
  )
}
