import React from 'react'
import { Link } from 'react-router-dom'

const NavBar = () => {
  return (

    <div class="container text-gray-400 mx-auto flex flex-wrap p-5 fixed bg-black flex-col md:flex-row items-center border-b-2 z-10 border-gray-300 max-w-full body-font">
      <nav class="flex lg:w-2/5 flex-wrap items-center text-base md:ml-auto">
        <Link to="/e3"><a class="mr-5 hover:text-white">E3</a></Link>
        <Link to="/a3s"><a class="mr-5 hover:text-white">A3S</a></Link>
        <Link to="/turing"><a class="mr-5 hover:text-white">Turing</a></Link>


      </nav>
      <a class="flex order-first lg:order-none lg:w-1/5 title-font font-medium items-center text-white lg:items-center lg:justify-center mb-4 md:mb-0">
        <Link to='/'>
          <span class="ml-3 text-xl xl:block lg:hidden">ENIGMA</span>
        </Link>
      </a>
      <div class="lg:w-2/5 inline-flex lg:justify-end ml-5 lg:ml-0">
        <button class="inline-flex items-center bg-blue-500 border-0 text-white py-1 px-3 focus:outline-none hover:bg-black hover:text-white rounded text-base mt-4 md:mt-0">Sign Up
          </button>
      </div>
    </div>

  )
}

export default NavBar
