import React from 'react'
import TuringImg from '../../assets/Turing.png'

const Turing = () => {
  return (
    <div>
      <section className='pb-10 md: py-10 h-screen bg-black flex flex-col items-center justify-center text-white'>
        <img className='w-8/12 md:w-4/12 mt-10' src={TuringImg} />
        <h className='text-3xl md:text-5xl mx-10 pb-3'>TURING</h>
        <h className='text-xl md:text-3xl mx-10 text-center py-5'>CURRENCY OF THE ENIGMA ECOSYSTEM</h>
      </section>
      <section className='bg-black h-screen text-white px-10 pt-32'>
        <p className='para text-white text-left text-lg md:text-2xl pb-20'>TURING (TNG) — named after the legendary British mathematician who broke the enigma cipher — is a virtual credit that can be redeemed for ENIGMA products and services. It powers the atomic pay-as-you-go pricing of ENIGMA and ensures a seamless user experience. No repeated charges to your card. Enables consolidated billing across ENIGMA services.</p>
        <h className='para text-white font-semibold'>INR → TNG Conversion Rate</h>
        <ul className='para list-disc text-lg md:text-xl   ml-5'>
          <li>Minimum TNG = 500</li>
          <li>Thresh = 5000</li>
          <li>INR = TNG * (1 + round((Thresh-TNG)/Thresh, 1))</li>
        </ul>
      </section>

    </div>
  )
}

export default Turing
