import React from 'react'
import { useMediaQuery } from 'react-responsive'
import MobileNavbar from './components/MobileNavbar/MobileNavbar'
import NavBar from './components/Navbar/Navbar'
import LandingPage from './containers/LandingPage/LandingPage'
import E3 from './containers/E3/E3'
import {
  HashRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import A3S from './containers/A3S/A3S'
import Turing from './containers/Turing/Turing'

//this component will replace app.js 
const Testing = () => {
  const isMobileDevice = useMediaQuery({ maxDeviceWidth: 800 });
  return (
    <>
      <Router>
        {!isMobileDevice && (
          <NavBar />
        )}
        {isMobileDevice && (
          <MobileNavbar />
        )}
        <Switch>
          <Route path="/" exact component={LandingPage} />
          <Route path="/e3" exact component={E3} />
          <Route path="/a3s" exact component={A3S} />
          <Route path="/turing" exact component={Turing} />

        </Switch>
      </Router>



    </>
  )
}

export default Testing
