import React from 'react'

const ContactUs = () => {
  return (
    <section className="section text-gray-400 bg-black body-font h-screen flex justify-center items-center">
      <div className="container justify-self-center m-auto flex flex-wrap flex-col justify-evenly text-white h-4/6 w-8/12 pt-20 md:pt-0">
        <h className="text-3xl text-left heading"> Get Early Access to ENIGMA</h>

        <form className="" noValidate>
          <input name="email" type="text" className="block h-16 px-4 form-input border-l-4 w-full sm:w-full xl:w-1/2 lg:w-4/5 md:w-full" placeholder="Email Address" />
          <button type="submit" className="block content-center h-16 bg-white my-6 text-black text-center form-button w-full sm:w-full xl:w-1/2 lg:w-4/5 md:w-full">Sign Up</button>

        </form>
      </div>
    </section>
  )
}

export default ContactUs
